@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body{font-family:'Roboto', sans-serif}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}

.book{margin-top:3rem}.book__label{display:flex;flex-direction:column;font-size:12px;margin-bottom:1rem;width:100%}.book__label--sm{width:45%}.book__input{border:1px solid #dfe3e4;border-radius:4px;margin-top:0.25rem;padding:0.5rem}.book__input--md{width:12.5rem}.book__input--sm{width:9.375rem}.book__textarea{border:1px solid #dfe3e4;border-radius:4px;margin-top:0.25rem;padding:0.5rem;resize:none;width:auto}.book__subgenre{display:flex;flex-direction:column;width:100%}.book__new-subgenre{align-items:center}.book__new-subgenre input[type="checkbox"]{cursor:pointer;margin-right:0.5rem}.book__new-subgenre--desc{font-size:14px}.lib__title{margin-bottom:2rem}.genres{flex-flow:wrap;width:33.5rem;margin-top:3rem}.button{background-color:white;border:none;cursor:pointer}.button__genre{border:1px solid #8c9a9e;border-radius:4px;margin-right:1rem;margin-bottom:0.75rem;padding:0.75rem 0.85rem;text-align:center;width:100px}.button__genre.active{background-color:#3498db;color:white}.button__genre:hover{border:#3498db 1px solid}.button__control{border-radius:4px;padding:.400rem .750rem;width:5.9375rem}.button__control--next{background-color:#3498db;color:white;margin-left:0.75rem}.button__control--next:hover{background-color:#217dbb}.button__control--back{color:rgba(0,0,0,0.6);background-color:#e0e1e2}.button__control--back:hover{color:rgba(0,0,0,0.8);background-color:#cacbcd}.button__icon{float:left;font-weight:bold;font-size:15px}.button__success{border-radius:4px;padding:.400rem .750rem;width:12.5rem}.buttons{justify-content:flex-end}:focus{outline:none}.step{position:relative}.step__counter{align-items:center;border-radius:50%;background:#dfe3e4;color:#8c9a9e;display:flex;font-weight:bold;height:44px;justify-content:center;margin-bottom:0.5rem;margin-right:7rem;width:44px}.step__counter::after{content:"";position:absolute;height:1px;width:80px;background-color:#8c9a9e;left:61px}.step__counter.active{background-color:#2ecc71;color:white}.step__text{display:flex;justify-content:center;width:44px}.steps .step:last-child .step__counter::after{content:none}.error{flex-direction:column}.error__msg{color:#b33f3d;font-size:12px;margin-top:.500rem;margin-left:.250rem}.success-page{display:flex;flex-direction:column;justify-content:center;text-align:center;padding:2rem}.success-page__logo{height:6.25rem}.success-page__btn{margin-top:3rem}.success-page__text{margin-top:2rem}body{font-family:"Roboto", sans-serif}h1{font-size:2rem}h2{font-size:1.5rem}h3{font-size:1.25rem}h4{font-size:1rem}h5{font-size:0.85rem}h6{font-size:0.65rem}.container{width:100%;height:100%;display:flex;justify-content:center}.grid-container{width:800px;height:100%;margin-top:4rem;border:1px solid gray;padding:1rem;border-radius:4px}.grid-x{display:flex;padding:0.5rem}::-webkit-input-placeholder{color:#c3cbcd;font-size:12x}:-ms-input-placeholder{color:#c3cbcd;font-size:12x}::-ms-input-placeholder{color:#c3cbcd;font-size:12x}::placeholder{color:#c3cbcd;font-size:12x}

